*{
  padding: 0;
  margin: 0;
  color: rgb(255, 255, 255);
}

html {
  scroll-behavior: smooth;
}


@media only screen and (max-width: 1000px) {
  .about-content{
    display: grid;
    gap: 12rem; 
  }
}

@media only screen and (max-width: 1200px) {
  .footer-con{
    text-align: center;
  }
  .footer-txt{
    display: grid;
    padding-bottom: 1rem;
  }
  .footer-econ{
    max-width: 200px;
    margin: 0 auto;
  }
  .about-content{
    display: grid;
  }
  .about-footer{
    padding-top: 1.8rem;
  }
  .navigation ul{
    padding-left: 0;
  }
  nav{
    position: absolute !important;
    top: 0px;
    left: -950px;
    max-width: 100%;
    min-height: 100%;
    padding: 4rem;
    transition: .3s all ease;
  }
  .front-main-content i{
    position: absolute;
    top: 10px;
    left: 15px;
    font-size: 2rem;
  }
  #close{
    position: absolute;
    top: -290px;
    left: -35px;
    cursor: pointer;
    transition-duration: 250ms;
  }
  .tog{
    position: absolute;
    top: 5px;
    left: 20px;
    font-size: 2rem;
    cursor: pointer;
    transition-duration: 250ms;
  }
  .tog:hover, #close:hover{
    color: rgba(255, 255, 255, 0.397);
  }
  ul{
    max-width: fit-content;
  }
  .absolute{
    position: absolute;
    z-index: -1;
    width: 100%;
    min-height: 100%;
    background-color: #000;
    opacity: 0;
    transition: .4 all ease;
  }
  #HOME{
    position: relative;
  }
}

@media only screen and (min-width: 1000px) {
  .front-main-content{
    display: flex;
  }
  .about-content{
    display: grid;
    grid-template-columns: 70% 30%;
  }
  .footer-con{
    display: flex;
  }
  .footer-econ{
      padding-right: 12rem;
  }
  .footer-txt{
    display: flex;
    justify-content: space-between;
    gap: 14rem;
  }
  nav, ul{
    display: flex;
  }
  nav{
    padding: 1.2rem 8rem;
  }
  #bars{
    display: none;
  }
  .front-main-content i{
    display: none;
  }
  #close{
    display: none;
  }
  .tog{
    display: none;
  }
}

#bars{
  font-size: 2rem;
}














body  {
  width: 100%;
  height: 100%;
  background: url('https://www.soumya-jit.tech/static/media/home-bg.dc52d5d40a40b13154b0.jpg') no-repeat center/cover;
}

 
.title a{
  text-decoration: none;
}

#container  {
  height: 100%;
  border: 1px solid rgba(255, 0, 0, 0);
  width: 100%;
  position: relative;
}

#container::after{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  min-height: 100vh;
  z-index: -1;
  background: rgba(0, 0, 0, 0.7);
  box-shadow: 120px 100px 250px #000000, inset -120px, -100px 250px #000000;
}

nav{
  font-size: 22px;
}

nav{
  justify-content: space-between;
  background: #18122777;
  backdrop-filter: blur(20px);
  max-width: 100%;
  transition: .2s;
}

.navigation, nav{
  color: rgb(255, 255, 255);
}

ul{
  justify-content: space-between;
  gap: 2rem;
  text-decoration: none;
  list-style-type: none;
}

ul li{
  padding: .3rem;
}

li a {
  text-decoration: none;
  color: white;
}

nav a:hover, a:focus {
  color: white;
}

li{
  position: relative;
}

li a:hover ~ .animation{
  width: 100%;
}

.animation{
  height: 5px;
  width: 0;
  background-color: #cd5ff8;
  position: absolute;
  bottom: 0;
  left: 0;
  transition: all .5s ease;
  border-radius: 8px;
}


nav h4, .navigation ul{
  margin: 0;
}

.front-main-content{
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
  margin: 5rem 0;
}

img{
  max-width: 100%;
}

.front-main-content img{
  width: 420px;
}

.front-text{
  font-size: 35px;
}












.about-content{
  padding: 5rem 0;
  margin: 10rem 0 1rem 0;
  font-size: 21px;
}

.about-content h2{
  text-align: center;
  margin-bottom: 4rem;
}

.about-logo{
  position: relative;
}

.about-logo img{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
















.footer-content{
  max-width: 350px;
  margin: auto;
  margin-bottom: 8rem;
  display: grid;
  text-align: center;
}

.footer-content i{
  font-size: 1.4rem;
  background-color: rgb(255, 255, 255);
  padding: .5rem;
  border-radius: 8rem;
  color: #700c86;
  transition: .2s all ease-in;
}

.footer-content i:hover{
  -webkit-box-shadow: 0px 0px 20px 0px rgb(241, 47, 209);
  -moz-box-shadow:    0px 0px 20px 0px rgb(226, 47, 181);
  box-shadow:         0px 0px 20px 0px rgb(216, 25, 130);
}

.footer-icon{
  display: flex;
  justify-content: space-between;
  gap: 2rem;
  max-width: 310px;
  margin: 0 auto;
  margin-top: 1.4rem;
}


















.footer-con{
  justify-content: space-between;
  padding: 0 5rem;
  background-color: #0a0416;
  padding: 1rem;
  padding-bottom: 1.1rem;
  font-size: 17px;
}

.footer-econ{
  display: flex;
  justify-content: space-between;
  gap: 2rem;
}

